@media (max-width: 30em) {
	.video-page {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.video-page iframe {
		height: 200px;
	}
}

@media (min-width: 30em) {
	.video-page {
		height: 100vh;
	}
}

.video-page {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.video-page iframe {
	-ms-flex: 3 1 0%;
	    flex: 3 1 0%;
}

.video-page > div {
	overflow-y: scroll;
	padding: 1em;
	white-space: nowrap;
	height: calc(100% - 2em);
}




